body {
  color: #111321;
  background-color: #fff;
  font-family: Manrope, sans-serif;
}

.img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  color: currentColor;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
}

li, ul {
  list-style: none;
}

button {
  cursor: pointer;
  margin: 0;
  padding: 0;
  display: block;
}

.container {
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
}

@media screen and (min-width: 768px) {
  .container {
    min-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1600px;
  }
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.banner {
  z-index: 2;
  opacity: 1;
  animation: banner-show .3s forwards;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes banner-show {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.banner_hide {
  pointer-events: none;
  opacity: 1;
  animation: banner-hidden .3s forwards;
}

@keyframes banner-hidden {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.banner__button {
  z-index: 20;
  width: 15px;
  height: 15px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: fixed;
  top: 5px;
  right: 5px;
}

@media screen and (min-width: 768px) {
  .banner__button {
    width: 25px;
    height: 25px;
    top: 10px;
    right: 10px;
  }
}

@media screen and (min-width: 1280px) {
  .banner__button {
    width: 40px;
    height: 40px;
    top: 15px;
    right: 15px;
  }
}

.banner__button-cross_icon {
